
import axios from "axios";
import store from '../../store/index'

export const officeController = {
    addOffice: async function (file, office) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("name[en]", office.name.en);
        formData.append("name[ar]", office.name.ar);
        formData.append("name[tr]", office.name.tr);
        formData.append("description[en]", office.description.en);
        formData.append("description[ar]", office.description.ar);
        formData.append("description[tr]", office.description.tr);
        formData.append("areas", office.areas);
        formData.append("nation_id", office.nation_id);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/office",
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    if (res.data.stats) {
                        state = true;
                        this.ShowAlert('secondary', 'success add office ')
                    }
                } else {
                    state = false;
                    this.ShowAlert('red', 'An Error ' + res.status);
                }
            }).catch(error => {
                state = false;
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error 😥 please contact Us '
                        + error
                })
            })
        return state;
    },
    getOffices: async function () {
        var mainUrl = store.state.mainUrl;
        var offices = [];
        await axios.get(mainUrl + "/office").then((data) => {
            if (data.status == 200) {
                offices = data.data.data;
            } else {
                this.ShowAlert('red', 'An ERROR ' + data.status)
            }
        }).catch(error => {
            this.ShowAlert('red', 'An ERROR ' + error)
        });
        // all time return below function for pass data between front and back end 
        return offices;
    },
    updateOffice: async function (file, office, areas) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("areas", areas);
        formData.append("nation_id", office.nation_id);
        formData.append("name[en]", office.name.en);
        formData.append("name[ar]", office.name.ar);
        formData.append("name[tr]", office.name.tr);
        formData.append("description[en]", office.description.en);
        formData.append("description[ar]", office.description.ar);
        formData.append("description[tr]", office.description.tr);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/office/" + office.id,
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                this.ShowAlert('secondary', 'done update success  ');
                if (res.data.state) {
                    this.ShowAlert('secondary', 'done update success  ');
                }
                state = true;
            }).catch(error => {
                state = false;
                this.ShowAlert('red', 'An Error 😥 please contact Us '
                    + error);
            })

        return state;
    },
    getAreas: async function () {
        var mainUrl = store.state.mainUrl;

        var areas = [];
        await axios.get(mainUrl + '/areas').then(data => {
            if (data.status == 200) {
                areas = data.data.data;
                store.commit('SET_AREAS', areas)
            } else {
                this.ShowAlert('red', 'An error  ERROR CODE ' + data.status);
            }

        }).catch(error => {
            this.ShowAlert('red', 'An ERROR area ' + error)
        });
        return areas;
    },

    deleteOffice: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/office/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ERROR CODE' + error);
            }
            );
        return state;
    },
    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        });
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }
};