import Vue from 'vue'
import VueRouter from 'vue-router'
import { routes } from './routs'
import store from '../store/index';
import VueCookies from "vue-cookies";

Vue.use(VueRouter)



const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  // localStorage.removeItem('token')
  store.commit('SET_TOKEN', VueCookies.get('token'));
  // var requiresAuth = to.meta.requiresAuth;
  var IsAuth = !!(store.getters.token);
  var user = VueCookies.get('user');
    store.commit('SET_USER', user);

  if (to.matched.some(record => record.meta.requiresAuth)) {
    // chick if user is auth 
    if (IsAuth) {
      // user is auth requiresRole
      next();
    } else {
      // user is not auth 
      next({ name: 'login' });
    }
  }

  // allow page to load
  else {
    // user is auth but vest login rout 
    if (to.name == 'login' && IsAuth) {
      next({ name: 'Home' });
    }
    next();
  }
})
export default router















































