import store from '../../store/index'
import axios from "axios"
import router from '.././../router/index'
import VueCookies from "vue-cookies";

export const authController = {
    login: async function (user) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("email", user.email);

        if (user.password) {

            formData.append("password", user.password);
        }

        await axios.request({
            method: 'POST',
            url: mainUrl + '/auth/login',
            data: formData,
            headers: {
                "Content-type": "multipart/form-data",
            }
        }).then(res => {
            if (res.data.stats) {
                VueCookies.set("token", res.data.token);
                VueCookies.set("user", res.data.user);
                store.commit('SET_USER', res.data.user);
                store.commit('SET_TOKEN', res.data.token);
                this.ShowAlert('secondary', 'Done logged IN success ')
                state = true;
            } else {
                this.ShowAlert('red', res.data.error)
            }
        }).catch(error => {
            localStorage.removeItem('token')
            localStorage.removeItem('user');
            var status = error.response.status;
            var state = (parseInt(status) == 401);
             if (state) {
                state = false;
                store.dispatch('showAlert', {
                    color: 'red', message: 'error  Unauthorized user '
                })
            }
            else {
                state = false;
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error chick if connection of hte internet  '
                        + error
                })
            }

        });
        return state;
    },
    logOut: async function () {
        var mainUrl = store.state.mainUrl;
        var token = VueCookies.get('token');

        if (token) {
            VueCookies.remove('user');
            VueCookies.remove('token');
            router.push({ name: '/login' })
            await axios.request({
                method: 'POST',
                url: mainUrl + '/auth/logout',
                headers: {
                    "Content-type": "multipart/form-data",
                    Authorization: "Bearer " + token,
                }
            }).then(res => {
                if (res.status == 200) {
                    this.ShowAlert('secondary', 'Done logged Out success ')

                    VueCookies.remove('token');
                    VueCookies.remove('user');

                    router.push({ name: 'login' })
                }
            }).catch(e => {
                console.log(e);
            })
        } else {
            router.push('/login')
        }
    },
    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
}