import Home from '../views/pages/Home.vue'
import { Role } from './roles'
import store from '../store/index'
import isAdmin from './middleware/admin'
export const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home,
        beforeEnter: (to, from, next) => isAdmin(to, from, next, store.getters.user.role_id),
        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.donations_manager, Role.informative] },
    },
    {
        path: '/projects',
        name: 'Projects',
        component: () => import('../views/pages/projects.vue'),
        beforeEnter: (to, from, next) => isAdmin(to, from, next, store.getters.user.role_id),
        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.informative] },
    },
    {
        path: '/offices',
        name: 'Offices',
        component: () => import('../views/pages/office.vue'),
        beforeEnter: (to, from, next) => isAdmin(to, from, next, store.getters.user.role_id),
        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.informative] },
    },
    {
        path: '/section',
        name: 'Section',
        component: () => import('../views/pages/sections.vue'),
        beforeEnter: (to, from, next) => isAdmin(to, from, next, store.getters.user.role_id),


        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.informative] },

    },
    {
        path: '/areas',
        name: 'Areas',
        component: () => import('../views/pages/areas.vue'),
        beforeEnter: (to, from, next) => isAdmin(to, from, next, store.getters.user.role_id),


        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.informative] },

    },
    {
        path: '/users',
        name: 'Users',
        component: () => import('../views/pages/user.vue'),
        beforeEnter: (to, from, next) => isAdmin(to, from, next, store.getters.user.role_id),

        meta: { requiresAuth: true, requiresRole: [Role.Admin] },
    },
    {
        path: '/camping',
        name: 'Camping',
        component: () => import('../views/pages/camping.vue'),
        beforeEnter: (to, from, next) => isAdmin(to, from, next, store.getters.user.role_id),

        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.donations_manager] },

    },
    {
        path: '/login',
        name: 'login',
        component: () => import('../views/auth/login.vue'),
    },
    {
        path: '/slider',
        name: 'slider',
        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.informative] },

        component: () => import('../views/pages/slider.vue'),
    },
    {
        path: '/testimonial',
        name: 'testimonial',
        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.informative] },

        component: () => import('../views/pages/testimonial.vue'),
    },
    {
        path: '/posts',
        name: 'posts',
        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.informative] },
        component: () => import('../views/pages/posts.vue'),
    },
    {
        path: '/setting',
        name: 'setting',
        meta: { requiresAuth: true, requiresRole: [Role.Admin, Role.informative] },
        component: () => import('../views/pages/setting.vue'),
    },
    // otherwise redirect to home
    { path: '*', redirect: '/' }

]