export default (to, from, next, role_user) => {
    console.log(role_user);
    var roles = to.meta.requiresRole;
    for (var i = 0; i < roles.length; i++) {
        if (roles[i] == role_user) {
            next();
            i = roles.length;
            return true;
        }
    }
    return false;
}