<script>
import { authController } from "./views/auth/auth";
import { officeController } from "./components/offices/office";
import VueCookies from "vue-cookies";

export default {
  mounted() {
    var lang = VueCookies.get("lang");
    if (lang) {
      if (lang == "ar") {
        this.$vuetify.rtl = true;
      } else {
        this.$vuetify.rtl = false;
      }
    } else {
      this.$vuetify.rtl = true;
      lang = "ar";
    }
    var lang_name = VueCookies.get("name_lang");
    if (!lang_name) {
      lang_name = "Arabic";
    }
    this.$store.commit("SET_LANG", lang);
    this.$store.commit("SET_LAN_NAME", lang_name);
    this.setSetting();
    this.$store.commit("SET_USER", VueCookies.get("user"));
    this.getAreas();
  },
  methods: {
    setThem(setting) {
      this.$vuetify.theme.themes.dark.primary = setting.primaryDark;
      this.$vuetify.theme.themes.dark.secondary = setting.secondaryDark;
      this.$vuetify.theme.themes.light.primary = setting.primaryLight;
      this.$vuetify.theme.themes.light.secondary = setting.secondaryLight;
    },
    setSetting() {
      var setting = VueCookies.get("setting");
      if (setting) {
        this.setThem(setting);
        this.$store.commit("SET_SETTING", setting);
        this.$vuetify.theme.dark = setting.dark;
      } else {
        setting = {
          dark: this.$vuetify.theme.dark,
          primaryDark: this.$vuetify.theme.themes.dark.primary,
          primaryLight: this.$vuetify.theme.themes.light.primary,
          secondaryDark: this.$vuetify.theme.themes.dark.secondary,
          secondaryLight: this.$vuetify.theme.themes.light.secondary,
        };
        VueCookies.set("setting", setting);
        this.$store.commit("SET_SETTING", setting);
      }
    },
    // changRight(val) {
    //   this.right = val;
    // },
    getAreas: async function () {
      var data = await officeController.getAreas();
      this.$store.commit("SET_AREAS", data);
    },
    closeAlert() {
      this.$store.state.isShowAlert = false;
    },
    logOut: async function () {
      await authController.logOut();
    },
  },
  computed: {
    lang() {
      var lan = this.$store.getters.lang;
      if (lan == "ar") {
        return true;
      } else {
        return false;
      }
    },
    user() {
      return this.$store.getters.user;
    },
    role() {
      if (this.user) {
        return this.user.role_id;
      }
      return 0;
    },
  },

  data() {
    return {
      //  value from front end
      miniVariant: false,
      clipped: false,
      drawer: true,
      rightDrawer: false,
      // right: true,
      // mainRight: false,
      // role: 4,
      items: [
        {
          mainIcon: "mdi-cast-variant",
          active: true,
          items: [
            { title: "Slider", route: "/slider" },
            { title: "Testimonial", route: "/testimonial" },
            { title: "Posts", route: "/posts" },
            { title: "Identifiers", route: "/setting" },
          ],
        },
      ],
      logoPath: require("./assets/images/logo.png"),
      //end value from front end
    };
  },
  components: {
    "sup-navigation-drawer": () =>
      import("./components/supNavigationDeawer.vue"),
  },
};
</script>

